.header {
  z-index: 100;
  background-image: url("header-background-mobile.a8ceff09.jpg");
  background-repeat: no-repeat;
  background-size: 100vw 100%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 125px;
  font-size: .875rem;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .5s ease-in-out;
  display: flex;
}

.header__nav {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px;
  display: flex;
}

.header__nav-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.header__nav-icon {
  stroke: #f5f5f5;
  margin-right: 5px;
}

.header__nav-icon:hover {
  stroke: #ff6b08;
}

.header__nav-title {
  display: none;
}

.header__nav-list {
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  display: flex;
}

.header__nav-language-btn {
  text-transform: uppercase;
  border: 1px solid;
  border-radius: 5px;
  padding: 2px;
  transition: all .3s ease-in-out;
}

.header__nav-language-btn:hover, .header__nav-language-btn:focus {
  filter: drop-shadow(0 8px 43px #ff6b0199);
  background: #ff6b08;
}

.header__nav-form {
  border-bottom: 1px solid;
  justify-content: center;
  align-items: center;
  gap: 3px;
  width: 280px;
  margin-top: 25px;
  display: flex;
}

.header__nav-input {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  padding: 4px;
}

.header__nav-input::placeholder {
  color: #f5f5f5;
}

.header__nav-input:focus + .header__nav-input-submit svg, .header__nav-input:hover + .header__nav-input-submit svg {
  stroke: getcolor("orange");
}

.header__nav-input--loupe {
  cursor: pointer;
  stroke: #ff6b08;
  transition: all .3s ease-in-out;
}

.header__error {
  color: #ff001b;
  white-space: nowrap;
  text-align: center;
  font-size: .75rem;
  display: none;
}

.underline {
  border-bottom: 2px solid #ff001b;
}

.header.scrolled {
  height: 40px;
  transition: all .6s ease-in-out;
}

.scrolled-container {
  padding: 0;
  transform: translateY(-6px);
}

.hovered {
  height: 216px;
  transition: all .6s ease-in-out;
}

.form-transform {
  transform: translateY(-10000px);
}

.nav-height {
  height: 40px;
  padding: 0;
}

.library-btn-list {
  position: relative;
  margin-top: 15px !important;
}

.buttons-transform {
  transform: translateY(-10000px);
}

@media screen and (width >= 768px) {
  .header {
    background-image: url("header-background-tablet.3b543a43.jpg");
    background-size: 100vw 108px;
    height: 108px;
    margin-bottom: 10px;
  }

  .header .header:hover {
    height: 108px;
  }

  .header--library {
