  }

  .header .header__nav-icon {
    margin-right: 5px;
  }

  .header .header__nav-container {
    width: 704px;
  }

  .header .header__nav-container > a {
    stroke: #fff;
    color: #fff;
    text-decoration: none;
    transition: all .5s ease-in-out;
  }

  .header .header__nav-container > a:hover {
    stroke: #ff6b08;
    color: #ff6b08;
  }

  .header__nav-form {
    width: 336px;
    margin: 0;
  }

  .header__error {
    text-align: center;
    width: 393px;
  }

  .header:hover {
    height: 216px;
  }

  .header:hover .header__error {
    padding-left: 12px;
  }
}

@media screen and (width >= 1280px) {
