.team__modal--close {
  fill: #403c3c;
  width: 35px;
  height: 35px;
  transition-property: fill border-color;
  background: none;
  border: none;
  padding: 4px;
  text-decoration: none;
  transition-duration: .25s;
  position: absolute;
